<template>
	<view class="uni-padding-wrap">
		
			<view >
				<uni-details  :options="approveData" :attachmentUrl="attachmentUrl" :flag="type" :user="user"></uni-details>
			</view>
			<view class="processList-box" v-if="procInstId">
				<view class="example-body">
					<uni-steps :options="processList" active-color="#333333" :active="active" direction="column" />
				</view>
			</view>
			<view v-if="index=='1'||index=='0'">
				<view class="button-sp-area" >
					<button class="mini-btn" type="primary" size="mini"   @click="ifAgree('Y')">同意</button>
					<button class="mini-btn" type="warn" size="mini"  @click="ifAgree('N')">不同意</button>
					<button class="mini-btn" type="default" size="mini">抄送</button>
				</view>
				<uni-popup :show="showtip" type="center" :mask-click="false" @change="change">
					<view class="uni-tip">
						<text class="uni-tip-title">{{opinion}}</text>
						<view class="uni-tip-content">
							<view class="uni-form-item uni-column">
								<input class="uni-input" focus placeholder="自动获得焦点" :value="opinion"/>
							</view>
							<!-- 报销 -->
							<view v-if="type=='3'">
								<view class="uni-form-item uni-column">
									<input class="uni-input" focus placeholder="扣款事由" v-model="reim.reason"/>
								</view>
								<view class="uni-form-item uni-column">
									<input class="uni-input" focus placeholder="扣款金额" v-model="reim.money"/>
								</view>
							</view>
							<!-- 付款 -->
							<view v-if="type=='170'">
								<view class="uni-form-item uni-column">
									<input class="uni-input" focus placeholder="扣款事由" v-model="reim.reason"/>
								</view>
								<view class="uni-form-item uni-column">
									<input class="uni-input" focus placeholder="扣款金额" v-model="reim.money"/>
								</view>
							</view>
						</view>
						<view class="uni-tip-group-button">
							<button class="uni-tip-button uni-tip-button1"     @click="cancel('tip','cancel')">取消</button>
							<button class="uni-tip-button uni-tip-button2"     @click="cancel('tip','yes')" :loading="loading1">确定</button>
						</view>
					</view>
				</uni-popup>
			</view>
			<view v-if="index=='2'">
				<view class="lastBox" v-for="(item,index) in lastList" :key="index">
					<view><text class="la-title">用户名称：</text>{{(item.node?item.node:{}).userName}}</view>
					<view v-if="item.comment"><text class="la-title">留言：</text>{{item.comment}}</view>
					<view v-else class="inputbox"><text class="la-title">留言：</text><input class="uni-input" focus placeholder="留言"  v-model="content[index]"/></view>
					<view><text class="la-title">附件：</text></view>
					<view class="button-sp-area" v-if="(item.node?item.node:{}).flag==true">
						<button class="mini-btn" type="primary" size="mini" @click="lastNode(index)">确认</button>
					</view>
				</view>
				
			</view>
		
	</view>
</template>
<script>
	import details from '@/common/details.js';
	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	import uniSteps from '@/components/uni-steps/uni-steps.vue'
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniDetails from '@/components/nui-details/nui-details.vue'
	import {
	    mapState
	} from 'vuex'
	export default {
		components: {
			uniIcons,
			uniPopup,
			uniSteps,
			uniDetails
		},
		data() {
			return {
				user:{},
				active:null,
				showtip: false,
				baseUrl:'',
				isLeaderPass:'',
				opinion:'',
				type: '',
				id: '',
				index:'',
				taskId: '',
				procInstId:'',
				approveData:{},
				attachmentUrl:[],
				processList:[],
				lastList:[],
				content:[],
				loading1:false,
				// 报销
				reim:{
					reason:'',
					money:0
			    },
				// 付款
				pay:{
					reason:'',
					money:0
				}
			}
		},
        computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
		onLoad(e) {
			this.type = e.type || '';
			this.id = e.id || '';
			this.taskId = e.taskId || '';
			this.index = e.index || '';
			console.log(e.type)
			let this_=this
			let detailUrl=''
			let header=''
			if(e.index=='0'){//催办
				detailUrl='system/sysUrge/setMapToPhone/'+e.id
				header='application/json;charset=UTF-8'
			}else if(e.index=='3'){//抄送
				detailUrl='system/sysCopy/setMapToPhone/'+e.id
				header='application/json;charset=UTF-8'
			}else{//审批，任务
				detailUrl='oa/task/queryEventDetail/'+e.type+'/'+e.id+'/'+e.taskId
				header=''
			}
			/**
			 * 详情页面数据接口
			 */
			this.urlRequestGet(detailUrl,header,function(res){
				console.log(res)
 				this_.approveData=res.data.phoneData
				this_.procInstId=res.data.phoneData.procInstId
				//流程---区分（催办的id不是当前这条数据的id）
				let pro_id=''
				if(this_.index=='0'||this_.index=='3'){//如果是(催办)点进来的，id赋值给后台传过来的id
					pro_id=res.data.phoneData.id
					if(res.data.phoneData.taskType=='1'){
						pro_id=res.data.phoneData.leaveId
					}
				}else{//如果不是(催办)点进来的，直接用URl带过来的id
					pro_id=this_.id
				}
				//流程
				this_.processListFun(res.data.phoneData.taskType,pro_id);
				this_.type=res.data.phoneData.taskType
				this_.user=res.data.user;
				if(e.index=='2'){//任务
					this.urlRequestPost('oa/oaProcExeNode/selectNodeVoTableInfo',{"actDefineId": res.data.phoneData.actDefineId,'objId':this_.id},null,function(res){
						this_.lastList=res.data.rows
					})
				}
			});
			
			/**  附件
			 */
			this.urlRequestPost('/oa/oaAttachment/selectAttachByObjectId',{"objId": this.id},null,function(res){
				this_.attachmentUrl=res.data.data
			})
			
		},
		methods: {
			/**
			 *  流程
			 */
			processListFun(type,id){
				let this_=this
				console.log(type)
				console.log(details.baseUrl(type))
				this.urlRequestPost(details.baseUrl(type)+'/processList',{"procInstId": this.procInstId,"id":id},"application/json;charset=UTF-8",function(res){
					console.log(res)
					this_.processList=res.data.rows
				})
			},
			/**
			 * 同意 不同意
			 */
			ifAgree(e){
				
				this.opinion=details.ifAgree(e)
				this['showtip'] = true
				this.isLeaderPass=e
			},
			/**
			 * 操作 弹框按钮
			 */
			cancel(type,flag) {
				this['showtip'] = false
				if(flag=='yes'){
					console.log(this.taskId)
					let this_=this;
					this_.loading1=true
					this.urlRequestPost(details.baseUrl(this.type)+'/completeCurTask',{"opinion": this.opinion,"isLeaderPass":this.isLeaderPass,"taskId":this.taskId,"id":this.id,"reason":this.reim.reason,"money":this.reim.money},null,function(res){
						uni.showToast({title:res.data.msg, icon:"none"});
						if(res.data.code=='0'){
							console.log(this_.index)
							uni.reLaunch({
							    url: '../../main/main'
							});
						}
						this_.loading1=false
					})
				}
			},
			change(e) {
				
				console.log('是否打开:' + e.show)
				if (!e.show) {
					this.showtip = false
				}
			},
			lastNode(index){
				console.log(index)
				let data={}
				data.parentId=this.id
				data.userId=this.lastList[index].node.userId
				data.content=this.content[index]
				data.attachmentList=[]
				console.log(data)
				let this_=this
				this.urlRequestPost(details.baseUrl(this.type)+'/insertExeComment',data,"application/json;charset=UTF-8",function(res){
					console.log(res)
					uni.showToast({title:res.data.msg, icon:"none"});
					if(res.data.code==0){
						uni.reLaunch({
						    url: '../../main/main'
						});
					}
				})
			}
		 },
		 onBackPress() {
		 	console.log('showtip', this.showtip);
		 	if (this.showtip) {
		 		this.showtip = false
		 		return true
		 	}
		 }
	}
</script>
<style>
	
	.image {
		width:200upx;
		margin:40upx 0;
	}
	.processList-box{
		margin-top: 25upx;
		padding: 10px 0;
		background-color: #F8F8F8;
	}
	button {
		margin-top: 30upx;
		margin-bottom: 30upx;
	}
	.button-sp-area {
		display: flex;
		justify-content: center
	}
	.mini-btn {
		/* flex: 1; */
	}
	/* 提示窗口 */
		.uni-tip {
			/* #ifndef APP-NVUE */
			display: flex;
			flex-direction: column;
			/* #endif */
			padding: 15px;
			width: 300px;
			background-color: #fff;
			border-radius: 10px;
		}
	
		.uni-tip-title {
			text-align: center;
			font-weight: bold;
			font-size: 16px;
			color: #333;
		}
	
		.uni-tip-content {
			/* padding: 15px;
	*/
			font-size: 14px;
			color: #666;
		}
	
		.uni-tip-group-button {
			/* #ifndef APP-NVUE */
			display: flex;
			/* #endif */
			flex-direction: row;
			margin-top: 20px;
		}
	
		.uni-tip-button {
			flex: 1;
			text-align: center;
			font-size: 14px;
			color: #3b4144;
			border-radius: 0 0 0 0;
			border: 0px;
			color: #FFFFFF;
		}
		.uni-tip-button1{
			background-color: #CCCCCC;
		}
		.uni-tip-button2{
			background-color: #4CA2FF;
		}
		.title {
			padding: 10upx 25upx;
		}
		.uni-icon-clear,
		.uni-icon-eye {
			color: #999;
		}
		.uni-input{
			background: #F7F7F7;
		}
		/* 最后节点操作 */
		.lastBox{
			margin-top: 30upx;
			padding: 20upx 32upx;
			/* background: #F7F7F7; */
			margin-bottom: 30upx;
			border: 1upx solid #F7F7F7;
		}
		.la-title{
			color: #666666;
			display: inline-block;
		}
		.uni-input{
			display: inline-block;
			background-color: #F7F7F7;
		}
		.inputbox{
			display: flex;
			justify-content: flex-start;
			align-items:center ;
			padding: 15upx 0;
		}
</style>

